import common from '../../util/common'

export default {
    name: 'Settings',
    data () {
        return {
            isDark: false,
            settings: [
                {
                    icon: 'arrow',
                    title: '退出登录',
                    type: 'exit'
                }, {
                    icon: 'text',
                    title: '书架排序',
                    type: 'sort'
                }, {
                    icon: 'switch',
                    title: '夜间模式',
                    type: 'nightMode'
                }, {
                    icon: 'arrow',
                    title: '清理缓存',
                    type: 'storage'
                }
            ],
            sortModal: false,
            sorts: [
                {
                    icon: 'cuIcon-read',
                    title: '最近阅读'
                },
                {
                    icon: 'cuIcon-time',
                    title: '更新时间'
                },
            ]
        }
    },
    computed: {
        sortType () {
            return this.$store.state.sortType
        },
    },
    methods: {
        settingBtn (type) {
            switch (type) {
                case 'exit':
                    uni.showLoading({
                        title: '注销中',
                        mask: true
                    })
                    try {
                        uni.clearStorageSync()
                        this.$store.commit('SET_USERINFO', null)
                    } catch (e) {
                        console.error(e)
                    } finally {
                        common.sleep(500)
                        uni.hideLoading()
                    }
                    break
                case 'sort':
                    this.sortModal = true
                    break
                case 'nightMode':
                    break
                case 'storage':
                    try {
                        uni.clearStorageSync();
                        uni.showToast({ title: '清理完成', duration: 1000})
                    } catch (e) {
                    }
                    break
            }
        },
        sortBtn (e) {
            this.$store.commit('SET_SORTTYPE', e.currentTarget.dataset.sort)
            uni.setStorage({
                key: 'sortType',
                data: e.currentTarget.dataset.sort
            })
            this.sortModal = false
        },
        nightModeBtn (e) {
            this.isDark = e.detail.value
        },
        hideSortModal () {
            this.sortModal = false
        }
    }
}
